.footer-lp8
	background: #F5F6F6
	padding: 50px 0 
	@media screen and(max-width:992px)
		padding: 40px  0 40px
	.container-fluid
		padding: 0  120px 
		@media screen and(max-width:1441px)
			padding: 0 100px 
		@media screen and(max-width:1025px)
			padding: 0 70px 
		@media screen and(max-width:768px)
			padding: 0 50px 
		@media screen and(max-width:576px)
			padding: 0 
	.row
		.col-12
			&:first-child
				p
					text-align: right
					@media screen and(max-width:992px)
						text-align: center
			&:last-child
				p
					text-align: left
					@media screen and(max-width:992px)
						text-align: center
	p
		font-size: 18px
		margin-bottom: 2px
		color: #000000
		text-align: center
		font-weight: 400
		@media screen and(max-width:992px)
			margin-bottom: 5px
			@media screen and(max-width:768.2px)
				font-size: 16px