.product-caption-lp
	overflow: hidden
	margin: 80px 0
	@media screen and(max-width:1441px)
		margin: 60px 0
	@media screen and(max-width:1025px)
		margin: 50px 0
	.container-fluid
		padding: 0 175px
		@media screen and(max-width:1441px)
			padding: 0  150px
		@media screen and(max-width:1367px)
			padding: 0 120px
		@media screen and(max-width:992px)
			padding: 0 100px
		@media screen and(max-width:768.2px)
			padding: 0 15px
	.caption-text-lp
		padding: 0 150px
		position: relative
		color: #0F549F
		font-size: 36px
		text-align: center
		&:last-child
			margin-bottom: 0 !important
		&:not(:last-child)
			margin-bottom: 100px
			&:before
				content: ''
				display: block
				position: absolute
				left: 50%
				bottom: -75px
				transform: translateX(-50%)
				width: 1px
				height: 50px
				background: #0F549F
				@media screen and(max-width:992px)
					height: 30px
					bottom: -55px
		@media screen and(max-width:1441px)
			font-size: 32px
			padding: 0 80px
		@media screen and(max-width:1281px)
			padding: 0 50px
		@media screen and(max-width:1025px)
			font-size: 24px
			padding: 0 30px
		@media screen and(max-width:992px)
			margin-bottom: 75px
		@media screen and(max-width:768.2px)
			padding: 0 15px
			margin-bottom: 75px
		@media screen and(max-width:576px)
			font-size: 26px
