.product-guide-lp
	overflow: hidden
	.container-fluid
		padding: 0 250px 0 400px
		position: relative
		&:before
			content: ''
			display: block
			position: absolute
			top: 10%
			right: 0
			z-index: -1
			width: 400px
			height: 600px
			background-image:  url('../img/landingpage8/right.png')
			background:  rgba( #ffffff,1)
			background-repeat: no-repeat
			background-size: contain
			background-position: top right 
			z-index: 0
			@media screen and(max-width:1025px)
				right: -10%
			@media screen and(max-width:992px)
				display: none
		&:after
			content: ''
			display: block
			position: absolute
			bottom: -2%
			left: 0
			z-index: -1
			width: 400px
			height: 600px
			background-image:  url('../img/landingpage8/left.png')
			background:  rgba( #ffffff,1)
			background-repeat: no-repeat
			background-size: contain
			background-position: top right
			@media screen and(max-width:992px)
				display: none
		@media screen and(max-width:1441px)
			padding: 0 250px 
		@media screen and(max-width:1367px)
			padding: 0 150px
		@media screen and(max-width:992px)
			padding: 0 100px
		@media screen and(max-width:768.2px)
			padding: 0 15px
	.guide-title-lp
		display: flex
		justify-content: center
		align-items: center
		flex-direction: column
	.product-guide-title-lp-8
		display: block
		font-size: 40px
		color: #184785
		text-align: center
		text-transform: uppercase
		@media screen and(max-width:1441px)
		@media screen and(max-width:1025px)
		@media screen and(max-width:576px)
			font-size: 30px
		&.border-bottom-lp
			display: inline-block
			margin: 100px 0 30px
			border-bottom: 1px solid #184785
			@media screen and(max-width:1441px)
				margin: 60px 0  20px
			@media screen and(max-width:1025px)
				margin: 50px 0 20px
	.product-guide-lp-wrapper
		.product-guide-image
			img
				max-width: 100%
				height: auto
				@media screen and(max-width:992px)
					max-height: 500px
					display: block
					margin: 0 auto
				@media screen and(max-width:768.2px)
					max-height: 400px
		.row
			position: relative
			@media screen and(max-width:992px)
				display: flex
				flex-direction: column
				.col-lg-5
					order: 2
				.col-lg-7
					order: 1

			.col-lg-5
				position: static
				.product-guide-caption-lp
					max-width: 40%
					position: absolute
					top: 50%
					transform: translateY(-50%)
					@media screen and(max-width:992.2px)
						position: static
						top: 0
						transform: none
						max-width: 100%
						margin: 50px 0 0 0
			&:nth-child(even)
				display: flex
				flex-direction: row-reverse
				@media screen and(max-width:992px)
					flex-direction: column
			
		.product-guide-caption-lp
			color: #0F549F
			font-size: 32px
			@media screen and(max-width:992.2px)
				margin: 0 auto
				text-align: center
				font-size: 26px