$main: #333333
html,body
	font-size: 13px
	color: #333333
	font-family: Nunito Sans
	@media (min-width: 768px) 
		font-size: 14px
	@media (min-width: 1025px) 
		font-size: 15px
	@media (min-width: 1441px)
		font-size: 16px
.center
	display: flex
	justify-content: center
	align-items: center
