.form-contact-lp8
	margin-bottom: 30px
	.container-fluid
		padding:  0 120px
		@media screen and(max-width:1025px)
			padding:  0 30px
		@media screen and(max-width:768.2px)
			padding: 0 15px
	h2
		font-size: 60px
		color: #184785
		text-transform: uppercase
		font-weight: 400
		text-align: center
		margin-bottom: 40px
		line-height: 1.35
		padding: 0 100px
		@media screen and(max-width:1441px)
			font-size: 48px
		@media screen and(max-width:1025px)
			font-size: 36px
			padding: 0 50px
		@media screen and(max-width:576px)
			padding: 0
		@media screen and(max-width:576px)
			font-size: 28px
			margin-bottom: 30px
	.wrap-form
		.form-group
			margin-bottom: 30px
			@media screen and(max-width:1441px)
				margin-bottom: 20px
			input
				height: 70px
				border: 2px solid #000000 
				color: #333333
				font-size: 22px
				@media screen and(max-width:1441px)
					height: 50px
					font-size: 20px
				@media screen and(max-width:992px)
					height: 40px
					font-size: 18px
				@media screen and(max-width:768px)
					font-size: 14px
				&:focus,&:hover
					outline: none
					box-shadow: none
					border-color: #000000
				&::placeholder
					font-size: 22px
					color: #000000
					opacity: 1 !important
					font-weight: 500
					@media screen and(max-width:1441px)
						font-size: 20px
					@media screen and(max-width:992px)
						font-size: 18px
					@media screen and(max-width:768px)
						font-size: 14px
				&:placeholder
					font-size: 22px
					color: #000000
					opacity: 1 !important
					font-weight: 500
					@media screen and(max-width:1441px)
						font-size: 20px
					@media screen and(max-width:992px)
						font-size: 18px
					@media screen and(max-width:768px)
						font-size: 14px

	.frm-btnwrap
		margin-top: 20px
		@media screen and(max-width:1441px)
			margin-bottom: 15px
		.frm-btn
			display: flex
			justify-content: center
			align-items: center
			input[type=submit]
				padding: 0 30px
				border: none
				background: #164988
				color: #ffffff
				font-size: 32px
				text-transform: uppercase
				font-weight: 500
				border-radius: 30px
				height: 60px
				width: 160px
				@media screen and(max-width:1441x)
					height: 50px
					width: 140px
				@media screen and(max-width:576px)
					height: 40px
					padding: 0 20px
					font-size: 24px