.product-intro-background
	background: url('../img/landingpage8/bg.jpg')
	background-repeat: no-repeat
	background-size: cover
	background-position: top
	@media screen and(max-width:992px)
		background: white
.button-wrapper
	display: flex
	justify-content: center
	align-items: center
.btn
	width: 600px
	height: 120px
	background: #164988
	text-transform: uppercase
	display: flex
	justify-content: center
	align-items: center
	font-size: 40px
	@media screen and(max-width:1441px)
		width: 300px
		height: 80px
		font-size: 30px
	@media screen and(max-width:1025px)
		height: 60px
		width: 250px
		font-size: 28px
.btn-buy
	color: #ffffff !important
