.product-intro-lp
	overflow: hidden
	padding: 230px 0 0 0
	@media screen and(max-width:1441px)
		padding: 80px 0 0 0
	.container-fluid
		padding: 0 130px !important
		@media( max-width:1441px)
			padding: 0 15px !important
	.nav-brand-lp-8
		margin-bottom: 130px
		@media screen and(max-width:1441px)
			margin-bottom: 50px
		a
			img
				max-width: 100%
				height: auto
				max-height: 124px
				display: block
				margin: 0 auto
				@media screen and(max-width:768px)
					max-height: 60px
	.nav-brand-new-lp8
		margin-bottom: 100px
		@media screen and(max-width:1441px)
			margin-bottom: 50px
		img
			max-width: 100%
			height: auto
			max-height: 37px
			display: block
			margin: 0 auto
	.title-eng-lp8
		font-family: "Times New Roman"
		font-size: 110px
		font-weight: 400
		color: #1456A4
		text-align: center
		margin-bottom: 90px
		@media screen and(max-width:1441px)
			margin-bottom: 50px
		sup
			vertical-align: middle
			font-size: -webkit-xxx-large
		@media screen and(max-width:1441px)
			font-size: 90px
		@media screen and(max-width:1025px)
			font-size: 60px
		@media screen and(max-width:576px)
			font-size: 48px
			sup
				font-size: 36px
		span
			display: block
	.caption-lp8
		margin: 90px 0
		font-size: 40px
		color: #0B2B4A
		line-height: 1.35
		text-align: center
		@media screen and(max-width:1441px)
			margin: 50px 0
			font-size: 30px
		@media screen and(max-width:1025px)
			font-size: 25px
.product-result-lp
	overflow: hidden
	.container-fluid
		padding: 0 130px !important
		@media screen and(max-width:1441px)
			padding: 0 15px !important
	img
		max-width: 100%
		height: auto
	.product-result-swiper,.product-result-swiper-wrapper
		position: relative
		.wrapper
			padding: 0 !important
			margin: 0 !important
			max-width: 100% !important
		.swiper-navigation
			width: 100%
			position: absolute
			z-index: 2
			top: 50%
			transform: translateY(-50%)
		.swiper-prev,.swiper-next
			cursor: pointer
			position: absolute
			top: 50%
			transform: translateY(-50%)
			display: block
			width: 70px
			height: 70px
			border-radius: 50%
			background: #ffffff
			display: flex
			justify-content: center
			align-items: center
			&:focus
				outline: none
			&:before
				content: ''
				font-family: 'Gotham-Light'
				font-size: 40px
				color: #164989
				font-weight: 100
			@media screen and(max-width:1441px)
				width: 50px
				height: 50px
				&:before
					font-size: 28px
			@media screen and(max-width:768.2px)
				width: 30px
				height: 30px
				&:before
					font-size: 20px
			
		.swiper-next
			right: -35px
			&:before
				content: '>'
			@media screen and(max-width:1441px)
				right: -25px
			@media screen and(max-width:1025px)
				right: 0
		.swiper-prev
			left: -35px
			&:before
				content: '<'
			@media screen and(max-width:1441px)
				left: -25px
			@media screen and(max-width:1025px)
				left: 0
	
			
	.list-lp
		display: block
		list-style-type: none
		padding: 0
		margin: 50px 0 0 0
		@media screen and(max-width:1025px)
			margin: 30px 0 0 0
		li
			text-align: center
			font-size: 30px
			color: #0b2b4a
			margin-bottom: 30px
			display: flex
			text-align: center
			justify-content: center
			align-items: flex-start
			@media screen and(max-width:1441px)
				margin-bottom: 20px
				font-size: 20px
			@media screen and(max-width:768px)
				margin-bottom: 15px
			&:before
				display: inline-block
				flex: 0 0 10px
				margin-right: 10px
				border-radius: 50%
				background: #0b2b4a
				width: 10px
				height: 10px
				content: ""
				margin-top: 20px
				@media screen and(max-width:1441px)
					margin-top: 10px
